.container{
  margin: 50px 0.2;
}

.card {
  margin-top: 50px !important;
  margin-bottom: 30px !important;
 
}
.infected {
  border-bottom: 30px solid rgba(9, 9, 233, 0.938);
}
.recovered {
  border-bottom: 30px solid rgba(29, 146, 5, 0.966);
}
.deaths {
  border-bottom: 30px solid rgba(202, 11, 11, 0.904);
}
@media(max-width:770px){
  .card{
    margin:2% 0 !important;
  }
} 